@import url('https://fonts.googleapis.com/css?family=Raleway:400,700');


 *, *:before, *:after {
	 box-sizing: border-box;
}
 body {
	 min-height: 100vh;
	 font-family: 'Raleway', sans-serif;
}
 .container {
	 position: absolute;
	 width: 100%;
	 height: 100%;
	 overflow: hidden;
}
 .container:hover .top:before, .container:active .top:before, .container:hover .bottom:before, .container:active .bottom:before, .container:hover .top:after, .container:active .top:after, .container:hover .bottom:after, .container:active .bottom:after {
	 margin-left: 200px;
	 transform-origin: -200px 50%;
	 transition-delay: 0s;
}
 .container:hover .center, .container:active .center {
	 opacity: 1;
	 transition-delay: 0.2s;
}
 .top:before, .bottom:before, .top:after, .bottom:after {
	 content: '';
	 display: block;
	 position: absolute;
	 width: 200vmax;
	 height: 200vmax;
	 top: 50%;
	 left: 50%;
	 margin-top: -100vmax;
	 transform-origin: 0 50%;
	 transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
	 z-index: 10;
	 opacity: 0.65;
	 transition-delay: 0.2s;
}
 .top:before {
	 transform: rotate(45deg);
	 background: #e46569;
}
 .top:after {
	 transform: rotate(135deg);
	 background: #ecaf81;
}
 .bottom:before {
	 transform: rotate(-45deg);
	 background: #60b8d4;
}
 .bottom:after {
	 transform: rotate(-135deg);
	 background: #3745b5;
}
 .center {
	 position: absolute;
	 width: 350px;
	 height: 350px;
	 top: 50%;
	 left: 50%;
	 margin-left: -175px;
	 margin-top: -200px;
	 display: flex;
	 flex-direction: column;
	 justify-content: center;
	 align-items: center;
	 padding: 30px;
	 opacity: 0;
	 transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
	 transition-delay: 0s;
	 color: #333;
}
 .center input {
	 width: 100%;
	 padding: 10px;
	 margin: 5px;
	 border-radius: 10px;
	 border: 1px solid #ccc;
	 font-family: inherit;
}

.google-auth{
     cursor:pointer;
     width: 100%;
	 padding: 10px;
	 margin: 5px;
	 border-radius: 10px;
	 border: 1px solid #ccc;
	 font-family: inherit;
}

.google-auth:hover {
	background:linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
	background-color:#e9e9e9;
} 
.facebook-auth:hover {
	background:linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
	background-color:#e9e9e9;
}
.facebook-auth{
    cursor:pointer;
    width: 100%;
    padding: 10px;
    margin: 5px;
    border-radius: 10px;
    border: 1px solid #ccc;
    font-family: inherit;
}


.inputBox input[type="submit"] {
	width: 100%;
    padding: 10px;
    margin: 10px;
    margin-top: 10px;
    margin-right: 100px;
    border-radius: 10px;
    border: 1px solid #ccc;
    font-family: inherit;
    background: #60b8d4;
    color: white;
    font-weight: 600;
    font-size: 15px;
    letter-spacing: 0.05em;
    cursor: pointer;
}

.inputBox input[type="submit"]:hover {
	background: #3745b5;
}

.links{
	position: relative;
	width: 100%;
	display: flex;
	justify-content: space-between;
	color:white;
	text-decoration: none;
}

.error-message {
	color: red;
}
